#SliderVideo {
  width: 100%;
  background-color: #F4F6F8;
  min-height: 84vh;
}
#SliderVideo .search {
  padding: 30px 14px;
}
#SliderVideo .ant-tabs-bar {
  border: none;
}
#SliderVideo .ant-tabs-nav-wrap {
  width: 320px;
  margin: 0 auto;
}
#SliderVideo .ant-input-affix-wrapper .ant-input-suffix {
  left: 12px;
  right: auto;
}
#SliderVideo .ant-input-affix-wrapper .ant-input:not(:last-child) {
  padding-right: 0;
  padding-left: 30px;
  outline: none;
}
#SliderVideo .ant-input:focus {
  outline: none;
  border: 1px solid #d9d9d9;
  box-shadow: none;
}
#SliderVideo .listLink a {
  padding-left: 33px;
  box-sizing: border-box;
  display: block;
}
#SliderVideo .listLink a p {
  color: #666666;
  font-size: 18px;
  padding: 12px 0;
  border-bottom: 1px solid #e7e7e7;
  font-weight: 500;
}
#SliderVideo .listLink .link-active {
  background-color: #F1948E;
}
#SliderVideo .listLink .link-active p {
  color: #fff;
  border-bottom: 1px solid #F1948E;
}
#SliderVideo .organize .ant-menu {
  background-color: #F4F6F8;
}
#SliderVideo .organize .ant-menu-inline .ant-menu-submenu > div {
  padding-left: 7px;
  box-sizing: border-box;
}
#SliderVideo .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title {
  margin: 0;
  height: 52px;
  line-height: 52px;
  padding-left: 40px;
  padding-right: 0;
  font-size: 20px;
}
#SliderVideo .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title span {
  font-size: 20px;
}
#SliderVideo .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title span i {
  font-size: 18px;
}
#SliderVideo .organize .ant-menu-inline .ant-menu-submenu .ant-menu-submenu-title .ant-menu-submenu-arrow {
  display: none;
}
#SliderVideo .organize .ant-menu-inline .ant-menu-submenu .ant-menu-sub .ant-menu-sub {
  padding-left: 40px !important;
}
#SliderVideo .organize .ant-menu-light > .ant-menu-submenu-open > div {
  background-color: #F1948E;
  color: #fff;
}
#SliderVideo .organize .ant-menu-item {
  font-size: 20px;
}
